<template>
  <div>
    <div>
      <el-card shadow='hover' class='card'>
        <router-view></router-view>
      </el-card>
    </div>

  </div>
</template>

<script>
export default {
  name: 'user-me',
  methods: {
    showCurrentUser () {
      this.$router.push({
        name: 'userMe'
      })
    },
    showCertainUser (userID) {
      this.$router.push({
        name: 'userAnother',
        params: {
          id: userID,
        }
      })
    }
  },
  mounted () {
    this.$bus.$on('showUser', this.showCertainUser);
  },
  beforeDestroy () {
    this.$bus.$off('showUser');
  }
}
</script>

<style scoped>
.card{
  width: 80%;
  margin-left: 10%;
  border-radius: 20px;
}
</style>